@use "../common/common.scss";

.refresh {
  position: fixed;
  left: 3rem;
  bottom: 3rem;
}

.refresh__button {
  position: sticky;
  bottom: 0;
  background: var(--red);
  border: none;
  height: 60px;
  width: 60px;
  border-radius: 50%;
  @include common.flex-center;
  cursor: pointer;
  transition: transform 0.15s;

  &:hover,
  &:focus-visible {
    transform: scale(1.13);
  }
}

@media (max-width: 700px) {
  .refresh {
    position: fixed;
    left: 2rem;
    bottom: 2rem;
  }
}

@media (max-width: 600px) {
  .refresh {
    position: fixed;
    left: 1rem;
    bottom: 4rem;
  }
}
